/// <reference path="./index.d.ts"/>

import React from "react";
import Masonry from "react-masonry-css";
import { mediaQueries } from "../../shared/config";
import "./styles.css";

import Tabletop from "tabletop";

import styled from "@emotion/styled";

const DesktopView = styled("div")`
  ${mediaQueries.mobile} {
    display: none;
  }
`;

const MobileView = styled("div")`
  ${mediaQueries.notMobile} {
    display: none;
  }
`;

interface CardLayoutState {
  sheetsData: any;
}

interface CardLayoutProps {
  columns: number;
  sheetId: string;
  cardComponent: React.ComponentClass<{ value: any }>;
}

export class CardLayout extends React.Component<
  CardLayoutProps,
  CardLayoutState
> {
  constructor(props: any) {
    super(props);
    this.state = {
      sheetsData: []
    };
  }
  componentDidMount() {
    const { sheetId } = this.props;
    Tabletop.init({
      key: sheetId,
      callback: (googleData: any) => {
        this.setState({
          sheetsData: googleData
        });
      },
      simpleSheet: true
    });
  }

  render() {
    const { columns } = this.props;
    return (
      <div>
        <MobileView>
          <Masonry
            breakpointCols={1}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column"
          >
            {this.state.sheetsData &&
              this.state.sheetsData.map((row: any) =>
                React.createElement(this.props.cardComponent, {
                  value: row
                })
              )}
          </Masonry>
        </MobileView>
        <DesktopView>
          <Masonry
            breakpointCols={columns}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column"
          >
            {this.state.sheetsData &&
              this.state.sheetsData.map((row: any) =>
                React.createElement(this.props.cardComponent, {
                  value: row
                })
              )}
          </Masonry>
        </DesktopView>
      </div>
    );
  }
}
